import instance from './instance'

import * as crypto from 'crypto'

const axios = instance()
const axiosClient = instance('/client')
export default class CommonServices {
  // static login (data) {
  //   return axios({
  //     url: '/auth/login',
  //     method: 'POST',
  //     data: { ...data, isAuthorized: false }
  //   })
  // }

  static login(data) {
    //for encryption of login payload
    const key = crypto
      .createHash('sha256')
      .update(String(process.env.REACT_APP_HASHING_PASSWORD))
      .digest('base64')
      .substring(0, 32)
    const cipherAlgorithm = 'aes-256-gcm'

    const encryptCredentials = (text) => {
      const initVector = crypto.randomBytes(16)
      const initVectorHex = initVector.toString('hex')
      const cipher = crypto.createCipheriv(cipherAlgorithm, key, initVector)
      const encoded = cipher.update(text, 'utf-8', 'hex') + cipher.final('hex')
      const authTag = cipher.getAuthTag().toString('hex')
      const metaAndEncoded = [authTag, initVectorHex, encoded].join('|')
      return metaAndEncoded
    }

    data.username = encryptCredentials(data.username)
    data.password = encryptCredentials(data.password)
    // end for encryption of login payload

    return axiosClient({
      url: '/api/login',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data: { ...data, isAuthorized: false },
    })
  }

  static logout(data) {
    return axiosClient({
      url: '/api/logout',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data,
    })
  }
  static getMe(data) {
    return axios({
      url: '/me',
      method: 'GET',
    })
  }
}
