import FileSaver from 'file-saver'
import XLSX from 'xlsx'

const saver = ({ fileName, extension, data }) => {
  // Existing Code
  // Trial
  // try {
  //   const { ipcRenderer } = window.require('electron')

  //   const reader = new FileReader()
  //   reader.readAsDataURL(data)
  //   reader.onloadend = function () {
  //     const data = reader.result
  //     const base64data = data.split(';base64,').pop()
  //     ipcRenderer.send('open-save-dialog', { data: base64data, fileName })
  //   }
  // } catch (error) {
  //   FileSaver.saveAs(data, `${fileName}.${extension}`)
  // }

  const FILE = `${fileName}.${extension}`

  try {
    console.log('SaveDialog start')
    const fs = require('fs')
    const { remote } = require('electron')
    const dialog = remote.dialog

    dialog.showSaveDialog({ defaultPath: FILE, filters: [{ name: 'All Files', extensions: ['*'] }] }).then(file => {
      // Stating whether dialog operation was cancelled or not.
      if (!file.canceled) {
        // Creating and Writing to the sample.txt file
        fs.writeFile(file.filePath.toString(), data, function (err) {
          console.log('SaveDialog success')
          if (err) {
            console.log('SaveDialog error', err)
            throw err
          }
        })
      }
    })
  } catch (error) {
    console.log('SaveDialog error', error)
    console.log('Going to FileSaver fallback')
    FileSaver.saveAs(data, FILE)
  }
}

const s2ab = s => {
  const buf = new ArrayBuffer(s.length) // convert s to arrayBuffer
  const view = new Uint8Array(buf) // create uint8array as viewer
  for (let i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF // convert to octet
  return buf
}

const fromBlob = props => {
  const { fileName = '', content, extension, mimeType = 'application/octet-stream' } = props

  // const blob = new Blob(content, { type: mimeType })
  let blob = null

  if (extension === 'xlsx') {
    const { method } = props

    const wb = XLSX.utils.book_new()
    const ws_data = content

    wb.SheetNames.push(fileName)

    const ws = XLSX.utils[method](ws_data)

    wb.Sheets[fileName] = ws

    const wbout = XLSX.write(wb, { bookType: 'xlsx', type: 'binary' })

    blob = new Blob([s2ab(wbout)], { type: mimeType })
  } else {
    blob = new Blob(content, { type: mimeType })
  }

  saver({ fileName, extension, data: blob })
}

const fromUrl = props => {
  const { fileName = '', content, extension, mimeType = 'application/octet-stream' } = props
  saver({ fileName, extension, data: content })
}

export { fromBlob, fromUrl }
