import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import clsx from 'clsx'
import classes from './CustomTextField.module.scss'
export default function CustomTextField(props) {
  const {
    label = '',
    required,
    readOnly,
    constainerStyle = {},
    error,
    onChange,
    type,
    noMargin = false,
    TextFieldStyle = {},
    className,

    ...other
  } = props
  const { value = '' } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError()
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
    if (typeof value === 'number') {
      if (value <= 0) setError(error)
    }
  }, [error, value])

  const handleOnChange = (e) => {
    setError()
    if (typeof onChange === 'function') onChange(e)
  }

  return (
    <div className={clsx(classes.root, className)} style={constainerStyle}>
      {label && (
        <Typography variant="subtitle2" className={classes.label}>
          {`${label} ${required ? '*' : ''}`}
        </Typography>
      )}
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography
            readOnly
            id={props.id}
            variant="subtitle2"
            className={`${classes.label} ${classes['read-only-value']}`}
          >
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['text-field']} ${noMargin ? classes['no-margin'] : ''} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field`}
              style={TextFieldStyle}
              onBlur={() => setFocus(false)}
            >
              <InputBase
                className={classes.input}
                onChange={handleOnChange}
                type={type}
                {...other}
                onFocus={() => setFocus(true)}
                autoComplete="off"
              />
            </Paper>
            {_error && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
