import { Components } from '@hims_client/hims-core'
import { setInvalidSession } from 'Pages/Protected/Store/actions'
import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import IdleTimer from 'react-idle-timer'
import { Typography } from '@material-ui/core'
import { StyledButton, StyledDialog, StyledDialogAction, StyledDialogContent } from './authSession.styles'

let dispatch = null

const AuthSessionService = {
  invalidSession: (value) => dispatch(setInvalidSession(value)),
}

const AuthSessionComponent = ({ logout }) => {
  const [showIdleModal, setShowIdleModal] = useState(false)

  dispatch = useDispatch()
  const invalidSession = useSelector((state) => state.authSession)

  const handleLogout = async () => {
    AuthSessionService.invalidSession(false)

    setShowIdleModal(false)
    await logout()
  }

  const handleOnIdle = () => {
    setShowIdleModal(true)
  }

  return (
    <>
      <Components.UI.UnauthorizedSessionModal isModalOpen={invalidSession} onClose={handleLogout} />

      <IdleTimer timeout={1000 * 60 * 15} onIdle={handleOnIdle} debounce={250} />
      {showIdleModal && (
        <StyledDialog open onClose={handleLogout}>
          <StyledDialogContent>
            <Typography>You have been logged out due to inactivity</Typography>
          </StyledDialogContent>
          <StyledDialogAction>
            <StyledButton variant="contained" color="primary" onClick={handleLogout}>
              Okay
            </StyledButton>
          </StyledDialogAction>
        </StyledDialog>
      )}
    </>
  )
}

export { AuthSessionComponent, AuthSessionService }
