import React, { Suspense } from 'react'
import { Switch, Route } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import CommonServices from 'Services/Api/CommonServices'
import { Provider } from 'react-redux'
import RbacController from '../../Controllers/RbacController'
import isElectron from 'is-electron'

// Compoents
import { AuthSessionComponent } from 'SharedComponents/AuthSession/AuthSession'
import HeaderComponent from 'SharedComponents/HeaderComponent/HeaderComponent'
import { SideBarComponent } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { PageTitleComponent } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsComponent } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingComponent, LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { PdfViewer2Modal } from 'SharedComponents/PdfViewer2/PdfViewer2'
import { IcdCptStatusComponent } from 'SharedComponents/IcdCptStatus/IcdCptStatus'

// Error Page
import { ErrorPageInit, NotFoundPage } from 'SharedComponents/ErrorPage/ErrorPage'

// classes
import classes from './Protected.module.scss'

// Store
import Store from './Store'

// Services
import ProtectedPageScrollEvent from 'Services/ProtectedPageScrollEvent'
import BaseUrlFetcher from 'Services/BaseUrlFetcher'

// Controllers
import ConfigController2 from 'Controllers/ConfigController2'
import LogController from 'Controllers/LogController'

// XURPAS
import { Components as XurpasComponents } from '@hims_client/hims-core'

// NOTE: these statements are left here for posterity/documentation purposes.
//       Do not include here your modules, it would make it memory intensive for the
//       React App to start and much more when it comes to building/compilation
// Modules
// import Claims from './Claims'
// import Billing from './BillingAndCollection'

// Modules
const Claims = React.lazy(() => import('./Claims'))
// TODO: uncomment Billing lazy loading
// const Billing = React.lazy(() => import('./BillingAndCollection'))
const Billing = null

export default function Protected({ history }) {
  const [selectedModule, setSelectedModule] = React.useState('')
  const [isAauthorized, setIsAuthorized] = React.useState(false)
  const [containerNode, setContainerNode] = React.useState(null)

  const getRoleIdPrefix = (module) => {
    let prefix = 'CL'
    switch (module) {
      case 'BILLING':
        prefix = 'REPLACE_THIS'
        break

      case 'PARTNER_NETWORK':
        prefix = 'REPLACE_THIS'
        break

      case 'CLAIMS':
        prefix = 'CL'
        break

      default:
        prefix = 'CL'
        break
    }

    return prefix
  }

  const getDefaultRoleName = (module) => {
    let role = 'BATCH_ENCODER'
    switch (module) {
      case 'BILLING':
        role = 'REPLACE_THIS'
        break

      case 'PARTNER_NETWORK':
        role = 'REPLACE_THIS'
        break

      case 'CLAIMS':
        role = 'BATCH_ENCODER'
        break

      default:
        role = 'BATCH_ENCODER'
        break
    }

    return role
  }

  const getMe = async () => {
    const user_data = await ConfigController2.getUserManagementData()

    if (!user_data) return resetToLogin()

    const { access_token, main_module, main_role, user_id, location } = user_data

    let module = main_module.toUpperCase().replace(/ +/g, '_')

    let role = main_role.toUpperCase().replace(/ +/g, '_')

    if (role != main_role) {
      console.error(
        'ROLE value had to be adjusted: [' +
          main_role +
          '] to [' +
          role +
          '].\n\n ROLE values should not have spaces and must be all-caps. ' +
          'This might have some effect on functionalities like escalation, task assignment.\n\n' +
          'Contact your administrator.'
      )
    }

    const { href, protocol, origin, pathname } = window.location
    let url = origin

    const isFromAwbLogin = window.localStorage.getItem('from_neo')

    if (isFromAwbLogin === 'true' || url.includes('localhost') || url.includes('127.0.0.1')) {
      // On local, it is assummed there is no other module
      console.log('Login was done on AWB login page and on local environment.')
    } else {
      // Check if "switch module" from another module was performed
      console.log('Login was via hims-app login SPA. Needs checking if module was launched from switch module')

      let path = pathname

      // Check if within hims-app electron
      if (protocol.includes('file')) {
        path = `${href.split('/build/')[1]}`
      }

      // Find module in URL path
      const urlModule = path
        .split('/')
        .find((element) => element.length > 1)
        .toUpperCase()

      // If module from IndexedDB does not match module from URL (it means, switch module was done)
      if (module !== urlModule) {
        module = urlModule

        console.log(
          'Switch Module was performed. Need to use first role for module. UM has limitation of not being able to distinguish between multiple roles under one module'
        )

        // Find first applicable role
        const data = await ConfigController2.getUserManagementData()
        role = data.role.find((element) => element.role_id.startsWith(getRoleIdPrefix()))
        role = !role ? getDefaultRoleName() : role.name.toUpperCase().replace(/ +/g, '_')
        console.log('has been switched. Using role: ', role)
      }
    }

    window.localStorage.setItem('token', access_token)
    window.localStorage.setItem('module', module)
    window.localStorage.setItem('role', role)
    window.localStorage.setItem('user_id', user_id)
    window.localStorage.setItem('user_location', location)
    window.localStorage.setItem('is_electron', isElectron())

    setSelectedModule(module)
    setIsAuthorized(true)

    const hash = window.location.hash

    if (hash === '#/') {
      if (!window.localStorage.getItem('from_login')) {
        window.localStorage.setItem('from_login', true)
      }
      // console.log('Navigated to Root')
      LogController.log({ module, level: 'debug', message: 'load-module', details: user_data }, user_id, access_token)
    }

    // TODO Billing Role , Policies Changes create separate function
    // const [err, res] = await to(CommonServices.getMe())
    // const stores = [
    //   { name: 'user_data', keyPath: ['key'] },
    //   { name: 'rbac_data', keyPath: ['key'] }
    // ]

    // if (err) {
    //   localStorage.removeItem('token')
    //   window.location.replace('#/login')
    // } else {
    //   const { data } = res.data
    //   console.log(data.username)
    //   console.log(UserData)
    //   const userLogged = UserData.filter(user => user.login.username === data.username)
    //   console.log(userLogged)
    //   RbacController.extractPolicies({ rbac: userLogged }).then(res => {
    //     console.log(res)
    //     const extractedPolicies = res
    //     BillingIndexedDB.connect({ dbName: 'billingAndCollection', stores }).then(res => {
    //       BillingIndexedDB.update({ db: res, storeName: 'rbac_data', data: { policies: extractedPolicies } })
    //     })
    //   })
    //   setSelectedModule(module)
    //   setIsAuthorized(true)
    // }
  }

  React.useEffect(() => {
    getMe()
  }, [])

  const routesForModule = (selectedModule) => {
    switch (selectedModule) {
      case 'CLAIMS':
        return (
          <Suspense fallback={<div>Loading...</div>}>
            <Route path="/" component={Claims} />
          </Suspense>
        )
      case 'BILLING':
        return (
          <Suspense fallback={<div>Loading...</div>}>
            <Route path="/" component={Billing} />
          </Suspense>
        )
      default:
        break
    }
  }

  const resetToLogin = async () => {
    const accessToken = window.localStorage.getItem('token')
    const userId = window.localStorage.getItem('user_id')

    await ConfigController2.clearUserManagementData()

    window.localStorage.removeItem('token')
    window.localStorage.removeItem('me')
    // window.localStorage.removeItem('from_login')
    window.localStorage.removeItem('api_token')
    window.localStorage.removeItem('pm_token')
    window.localStorage.removeItem('user_id')
    window.localStorage.removeItem('employee_id')
    window.localStorage.removeItem('first_name')
    window.localStorage.removeItem('last_name')

    const fromNeo = window.localStorage.getItem('from_neo')
    window.localStorage.removeItem('from_neo')
    window.localStorage.setItem('from_login', true)

    // Do not delete this logging of location information.
    // This is helpful since logout feature supports at least 3 circumstances:
    // - logout from local solo devt (outside of hims-app)
    // - logout from hims-app (web deployed)
    // - logout from hims-app (electron deployed)
    console.log(window.location)

    const { href, protocol, origin } = window.location
    let url = origin
    let targetUrl = ''

    if (fromNeo === 'true' || url.includes('localhost') || url.includes('127.0.0.1')) {
      // Logout redirection for login coming from NEO login or local solo devt
      targetUrl = `${url}#/login`
    } else if (protocol.includes('file')) {
      // Logout redirection for login coming from hims-app Login SPA (Electron built)
      targetUrl = `${href.split('/build/')[0]}/build/index.html`
    } else {
      // Logout redirection for login coming from hims-app Login SPA (Web deployed)
      targetUrl = '/'
    }
    window.location.assign(targetUrl)

    LogController.log(
      {
        module: selectedModule,
        level: 'debug',
        message: 'load-login',
        details: { target_url: targetUrl, user_id: userId, access_token: accessToken },
      },
      accessToken,
      userId
    )
  }

  const logout = () => {
    LoadingService.loading(true)
    RbacController.clearRbacData()
    const accessToken = window.localStorage.getItem('token')
    const userId = window.localStorage.getItem('user_id')

    LogController.log(
      {
        module: selectedModule,
        level: 'debug',
        message: 'logout',
        details: { user_id: userId, access_token: accessToken },
      },
      userId,
      accessToken
    )

    if (!accessToken) return resetToLogin()

    CommonServices.logout({ access_token: accessToken })
      .then(() => resetToLogin())
      .catch(({ response }) => {
        try {
          const { error } = response.data
          if (error.statusCode === 428) resetToLogin()
        } catch (err) {
          resetToLogin()
        }
      })
  }

  const scrollContainer = (event) => {
    const { scrollTop } = containerNode
    containerNode.scrollTo({ top: scrollTop + event.deltaY })
  }

  const onUploadAvatarSuccess = (event) => {
    console.log('CLAIMS onUploadAvatarSuccess called')
    return true
  }

  return (
    <React.Fragment>
      {isAauthorized && (
        <div className={classes['main-container']}>
          <Provider store={Store}>
            <SideBarComponent />
            <PdfViewer2Modal />
          </Provider>

          <div className={classes['sub-container']}>
            <HeaderComponent logout={logout} />

            <Provider store={Store}>
              <AuthSessionComponent logout={logout} />
            </Provider>

            <Switch>
              <Route path="/404" exact component={NotFoundPage} />
              <Route
                path="/profile"
                render={(props) => (
                  <div className={classes.content}>
                    <Provider store={Store}>
                      <BreadcrumbsComponent />
                      <LoadingComponent onScrollContainer={scrollContainer} containerNode={containerNode} />
                    </Provider>
                    <Switch>
                      <React.Fragment>{routesForModule(selectedModule)}</React.Fragment>
                    </Switch>

                    <XurpasComponents.UI.ProfilePage
                      url={BaseUrlFetcher.getRawClientBaseUrl()}
                      onUploadSuccess={onUploadAvatarSuccess}
                    />
                  </div>
                )}
              />
              <Route
                path="/"
                render={(props) => (
                  <Grid
                    item
                    id={'content'}
                    className={classes.content}
                    onScroll={ProtectedPageScrollEvent.publish}
                    ref={setContainerNode}
                  >
                    <Provider store={Store}>
                      {/* <SnackbarComponent /> */}
                      <PageTitleComponent />
                      <BreadcrumbsComponent />
                      <LoadingComponent onScrollContainer={scrollContainer} containerNode={containerNode} />
                    </Provider>
                    <Switch>
                      <React.Fragment>{routesForModule(selectedModule)}</React.Fragment>
                    </Switch>
                  </Grid>
                )}
              />
            </Switch>
          </div>
        </div>
      )}
      <ErrorPageInit />
      <IcdCptStatusComponent />
    </React.Fragment>
  )
}
